export const START_YEAR = 1980;
export const END_YEAR = 2022;


export const getYearsRange = () => {
    let from = START_YEAR;
    let to = END_YEAR;

    let resultArr: number[] = [];
    for (let i = from; i <= to; i++) {
        resultArr.push(i);
    }
    return resultArr.reverse();
}

